
import React, {useState} from 'react'
import { Data } from './Data';
import styled from 'styled-components';
import { IconContext } from 'react-icons';
import { FiPlus, FiMinus } from 'react-icons/fi';
import Fade from 'react-reveal/Fade';
import "./questions.scss";

const media = {
    desktop: '@media(max-width: 768px)'
}


const AccordionSection = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;
    position: relative;
    margin-top: -220px;
    margin-bottom: 200px ;
    height:130vh;
    
    
    `;

const Container = styled.div`
    position: absolute;
    top: 30%;
    max-width: 1200px ;
    
    box-shadow: 2px 10px 35px 1px rgba(153, 153, 153, 0.3);
`;
const Wrap = styled.div`
    background: #1a1a1a;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
    width: 100%;
    text-align: center;
    padding: 0px 25px ;
    cursor: pointer;

    ${media.desktop} {
            text-align: left;
        }

    h1{
        
        font-family: PoppinsLight;
        padding: 2rem ;
        font-size: 1.2rem;

        ${media.desktop} {
            font-size: 1rem;
            padding: 2rem 1rem;
        }
        

    }

`;

const Dropdown = styled.div`
    font-family: PoppinsLight;
    background: #101010;
    color: #fbba00;
    width: 100%;
    height: 100px;
    display: flex;
    padding: 0px 120px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #fbba00;
    border-top: 1px solid #fbba00;
    font-size:500px;
    
    
    ${media.desktop} {
            height: 100%;
            padding: 10px 30px;
            font-size: 1px;
        }

    p{
        font-size: 1rem;
    }
`;


const Questions = () => {
    const [clicked, setClicked] = useState(false)

    const toggle = index => {
        if(clicked === index ) {
            return setClicked(null)
        }

        setClicked(index)
    }
  return (
    <>
            

    <IconContext.Provider value={{ color: '#fbba00',
    size: '25px'}}>
        <div className="questions__title" id="faq">
             <Fade top>
                <h1 className="title">PREGUNTAS FRECUENTES</h1>
                <h2 className="subtitle">¡RESOLVAMOS TUS DUDAS!</h2>
                </Fade>
                    
             </div>
        <AccordionSection>
            <Container>
                {Data.map((item, index) => {
                    return(
                        <>
                        <Wrap onClick={() => toggle(index)} key=
                        {index} >
                            <h1 key={item.id}>{item.question} </h1>
                            <span>{clicked === index ? <FiMinus /> : <FiPlus />} </span>
                        </Wrap>
                        {clicked === index ? (
                             <Dropdown>

                             <p>{item.answer} </p>
                            </Dropdown>
                        ) : null }
                       
                        </>
                    )
                })}

            </Container>
        </AccordionSection>

    </IconContext.Provider>
    </>
  )
}

export default Questions