import React from 'react';
import Fade from 'react-reveal/Fade';
import "./artist.scss"

export default function Artist() {
    return (
        <div className="artist" id="artist">
            
           
            <div className="artist__title">

            <Fade top>
                <h1 className="title">ARTISTA</h1>
                </Fade>
                <img className="guion" src="static/media/guion.png" alt=""/>
            </div>
          
           <div className="artist__banner">
                <Fade left>
                <div className="banner__hijo">
                    <div className="figura">
                        <img src="static/media/comm.png" alt="" />
                    </div>
                </div>
                </Fade>
                <Fade right>
                <div className="banner__hijo">
                    <div className="nombre">


                        <div className="nombre__titulo">
                            {/* <div className="miguel">
                                <span>MIGUEL</span>
                            </div> */}
                            <img className="images"
                            src="static/media/casas.png" alt=""/>
                        </div>
                        <div className="last">
                            <p>4 años en la industria de los tatuajes. Como Tatuador considero una expresión de libertad que a través de la tinta, te permite vivir una experiencia única. 
                            </p>
                            <div className="boton__portafolio">
                                
                                <a href="#portfolio"><button > VER PORTAFOLIO </button></a>
                            </div>
                        </div>
                    </div>

                </div>
                </Fade>



            </div>
            <div className="maquina">
                <img src="static/media/mac.png" alt="" />
            </div>
        </div>
    )
}
