import "./testimonials.scss";
import Fade from 'react-reveal/Fade';

export default function Testimonials() {
    return (
        <div className="testimonials" id="testimonials">
             <div className="testimonials__title">
             <Fade top>
                <h1 className="title">TESTIMONIOS</h1>
                </Fade>
                     <img className="guion" src="static/media/guion.png" alt=""/> 
             </div>
             
             <div className="container">
             <Fade bottom cascade>
                 <div className="card">
                     <div className="card__wrapper">
                        <div className="card__image">
                            <img src="static/media/img1.png" className="image__user" alt="" />
                            <img src="static/media/stars.png" alt=""  className="image__star"/>
                        </div>
                        <p>“Mi primer tatuaje tenia que ser especial, y gracias a la paciencia y talento de Miguel, pude conseguirlo (: Estoy planeando regresar el proximo mes, para mi cumpleaños”</p>
                        <h1>Daniella Almeida</h1>
                      
                       
                        
                        
                     </div>
                 </div>
                 <div className="card">
                     <div className="card__wrapper">
                        <div className="card__image">
                            <img src="static/media/img2.png" className="image__user" alt="" />
                            <img src="static/media/stars.png" alt="" />
                        </div>
                        <p>“Habían pasado más de 4 años desde mi último tatuaje, por una mala experiencia. Sin embargo, conocí el estudio Dirty Ink, me tatué y superó mis expectativas, ¡Felicitaciones!”</p>
                        <h1>John Sanchez</h1>
                      
                       
                        
                        
                     </div>
                 </div>
                 <div className="card">
                     <div className="card__wrapper">
                        <div className="card__image">
                            <img src="static/media/img3.png" className="image__user" alt="" />
                            <img src="static/media/stars.png" alt="" />
                        </div>
                        <p>“Después de muchos años vuelvo al Perú y lo primero que hice, después de ver a mi familia, fue tatuarme :D Llegué al estudio de Miguel por la recomendación de una amiga.”</p>
                        <h1>Cristina Oliveira</h1>
                      
                       
                        
                        
                     </div>
                 </div>
                 </Fade>
             </div>
            
        </div>
    )
}
